import { Dimensions, Text, View, StyleSheet, Image } from "react-native";
import Carousel from "react-native-reanimated-carousel";
import { LinearGradient } from "expo-linear-gradient";
import React from "react";
import getMetaInfo from "./getMetaInfo.json";

const width = Dimensions.get("window").width;

export default function CarouselCardNew() {
  const bottomPanel = getMetaInfo.data[0].getMetaInfo.bottomPanel;
  return (
    <View>
      <Carousel
        loop
        width={300}
        height={100}
        autoPlay={false}
        autoPlayInterval={2500}
        data={bottomPanel.card}
        scrollAnimationDuration={1000}
        //onSnapToItem={(index) => console.log("current index:", index)}
        renderItem={({ item, index }) => (
          <LinearGradient
            colors={item.background[0].color}
            style={styles.linearGradient}
          >
            <View
              style={{
                flex: 1,
                borderWidth: 1,
                flexDirection: "row",
                alignItems: "center",
              }}
            >
              <View>
                {item.imageAvatar && item.imageAvatar.length ? (
                  <Image
                    source={require("../../assets/pic.png")}
                    style={{
                      width: `${item.imageAvatar[0].width}`,
                      height: `${item.imageAvatar[0].height}`,
                      margin: `${item.imageAvatar[0].margin}`,
                    }}
                  />
                ) : null}
              </View>
              <View>
                <Text
                  style={{
                    fontSize: `${item.textStyle[1].fontSize}`,
                    color: `${item.textStyle[1].fontColor}`,
                    fontFamily: `${item.textStyle[1].fontType}`,
                    fontWeight: `${item.textStyle[1].fontWeight}`,
                    marginLeft: `${item.textStyle[1].margin}`,
                  }}
                >
                  {item.title}
                </Text>

                <View>
                  <Text
                    style={{
                      fontSize: `${item.textStyle[0].fontSize}`,
                      color: `${item.textStyle[0].fontColor}`,
                      fontFamily: `${item.textStyle[0].fontType}`,
                      fontWeight: `${item.textStyle[0].fontWeight}`,
                      width: 200,
                      marginLeft: 10,
                      marginTop: 4,
                      justifyContent: "space-between",
                    }}
                  >
                    {item.message}
                  </Text>
                </View>
              </View>
            </View>
          </LinearGradient>
        )}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  linearGradient: {
    borderRadius: 5,
    height: 80,
  },
});
