import { StyleSheet, Text, View } from "react-native";
import React, { useState } from "react";
import { LinearGradient } from "expo-linear-gradient";
import getMetaInfo from "./getMetaInfo.json";
import { Button } from "react-native-paper";
import Header from "./Header";
import Footer from "./Footer";
import Main from "./Main";
export default function Startup() {
  const questionOptions =
    getMetaInfo.data[0].getMetaInfo.middlePanel.activeContest.question.option;
  const contestQuestion =
    getMetaInfo.data[0].getMetaInfo.middlePanel.activeContest;
  const middlePanel = getMetaInfo.data[0].getMetaInfo.middlePanel;
  const [buttonStatus, setButtonStatus] = useState(false);
  const playNow = () => {
    setButtonStatus(true);
  };
  return (
    <View
      style={{
        backgroundColor: "rgba(0, 0, 0, 0.3)",
        borderLeftColor: "1px solid rgba(255, 255, 255, 0.4)",
        justifyContent: "flex-start",
        margin: "4%",
        width: "300px",
      }}
    >
      {!buttonStatus ? (
        <View>
          <LinearGradient
            colors={middlePanel.activeContest.question.background[0].color}
            style={{
              borderRadius: `${contestQuestion.question.background[0].borderRadius}`,
            }}
          >
            <Text
              style={{
                fontSize: `${contestQuestion.question.textStyle[0].fontSize}`,
                fontFamily: `${contestQuestion.question.textStyle[0].fontType}`,
                fontWeight: `${contestQuestion.question.textStyle[0].fontWeight}`,
                color: `${contestQuestion.question.textStyle[0].fontColor}`,
                padding: `${contestQuestion.question.textStyle[0].padding}`,
                //width: `${contestQuestion.question.textStyle[0].width}`,
                textAlign: "center",
              }}
            >
              THINK YOURE AN MVP?
            </Text>
          </LinearGradient>
          <Button
            mode="contained"
            style={{
              backgroundColor: `${contestQuestion.button.background[0].color[0]}`,
              borderRadius: `${contestQuestion.button.buttonStyle.borderRadius}`,
              width: `${contestQuestion.button.buttonStyle.width}`,
              marginLeft: `${contestQuestion.button.buttonStyle.marginLeft}`,
              marginTop: `${contestQuestion.button.buttonStyle.marginTop}`,
            }}
            onPress={() => playNow()}
          >
            <Text
              style={{
                color: `${contestQuestion.button.textStyle[0].fontColor}`,
                textAlign: `${contestQuestion.button.textStyle[0].textAlign}`,
                fontWeight: `${contestQuestion.button.textStyle[0].fontWeight}`,
                fontFamily: `${contestQuestion.button.textStyle[0].fontType}`,
              }}
            >
              {contestQuestion.button.label}
            </Text>
          </Button>
        </View>
      ) : (
        <View>
          <Header />
          <Main />
          <Footer />
        </View>
      )}
    </View>
  );
}

const styles = StyleSheet.create({});
