import { ScrollView } from "react-native-gesture-handler";
import { StyleSheet, Text, View } from "react-native";
import React, { useState } from "react";
import { RadioButton, Button } from "react-native-paper";
import { LinearGradient } from "expo-linear-gradient";
import getMetaInfo from "./getMetaInfo.json";

export default function Questions({ showQuestions, childToParent }) {
  const [value, setValue] = useState(null);
  const [buttonStatus, setButtonStatus] = useState(false);
  const questionOptions =
    getMetaInfo.data[0].getMetaInfo.middlePanel.activeContest.question.option;
  const contestQuestion =
    getMetaInfo.data[0].getMetaInfo.middlePanel.activeContest;

  const selectedQuestion = (question) => {
    // debugger;
    setValue(question);
    setButtonStatus(true);
  };
  const playNow = () => {
    //debugger;
    showQuestions.status = "true";
    childToParent("true", showQuestions);
  };
  return (
    <ScrollView>
      <View
        style={{
          width: `${contestQuestion.question.width}`,
          margin: `${contestQuestion.question.margin}`,
        }}
      >
        <LinearGradient
          colors={contestQuestion.question.background[0].color}
          style={{
            borderRadius: `${contestQuestion.question.background[0].borderRadius}`,
            marginLeft: "2%",
            marginRight: "4%",
            marginTop: "2%",
          }}
        >
          <Text
            style={{
              fontSize: `${contestQuestion.question.textStyle[0].fontSize}`,
              fontFamily: `${contestQuestion.question.textStyle[0].fontType}`,
              fontWeight: `${contestQuestion.question.textStyle[0].fontWeight}`,
              color: `${contestQuestion.question.textStyle[0].fontColor}`,
              padding: `${contestQuestion.question.textStyle[0].padding}`,
              width: `${contestQuestion.question.textStyle[0].width}`,
            }}
          >
            {showQuestions.name}
          </Text>
        </LinearGradient>
        {showQuestions.questions.map((item) => {
          return (
            <RadioButton.Group
              onValueChange={(newValue) => selectedQuestion(newValue)}
              value={value}
              key={item.key}
            >
              <View
                style={{
                  backgroundColor: `${questionOptions.background[0].color[0]}`,
                  width: `${questionOptions.background[0].width}`,
                  height: `${questionOptions.background[0].height}`,
                  borderRadius: `${questionOptions.background[0].borderRadius}`,
                  flexDirection: `${questionOptions.background[0].flexDirection}`,
                  alignItems: `${questionOptions.background[0].alignItems}`,
                  marginTop: `${questionOptions.background[0].marginTop}`,
                  // marginRight: `${questionOptions.background[0].marginRight}`,
                  fontSize: `${questionOptions.background[0].fontSize}`,
                  marginLeft: "2%",
                }}
              >
                <RadioButton
                  value={item.key}
                  color="white"
                  uncheckedColor="white"
                />

                <Text
                  style={{
                    width: `${questionOptions.textStyle[0].width}`,
                    color: `${questionOptions.textStyle[0].fontColor}`,
                    padding: `${questionOptions.textStyle[0].padding}`,
                    fontSize: `${questionOptions.textStyle[0].fontSize}`,
                    fontFamily: `${questionOptions.textStyle[0].fontType}`,
                    fontWeight: `${questionOptions.textStyle[0].fontWeight}`,
                  }}
                >
                  {item.key}
                </Text>
              </View>
            </RadioButton.Group>
          );
        })}
        <Button
          mode="contained"
          style={
            !buttonStatus
              ? {
                  backgroundColor: `${contestQuestion.button.background[0].color[0]}`,
                  borderRadius: `${contestQuestion.button.buttonStyle.borderRadius}`,
                  width: `${contestQuestion.button.buttonStyle.width}`,
                  marginLeft: `${contestQuestion.button.buttonStyle.marginLeft}`,
                  marginTop: `${contestQuestion.button.buttonStyle.marginTop}`,
                }
              : {
                  backgroundColor: `${contestQuestion.button.background[1].color[0]}`,
                  borderRadius: `${contestQuestion.button.buttonStyle.borderRadius}`,
                  width: `${contestQuestion.button.buttonStyle.width}`,
                  marginLeft: `${contestQuestion.button.buttonStyle.marginLeft}`,
                  marginTop: `${contestQuestion.button.buttonStyle.marginTop}`,
                }
          }
          onPress={() => playNow()}
        >
          <Text
            style={
              !buttonStatus
                ? {
                    color: `${contestQuestion.button.textStyle[0].fontColor}`,
                    textAlign: `${contestQuestion.button.textStyle[0].textAlign}`,
                    fontWeight: `${contestQuestion.button.textStyle[0].fontWeight}`,
                    fontFamily: `${contestQuestion.button.textStyle[0].fontType}`,
                  }
                : {
                    color: `${contestQuestion.button.textStyle[1].fontColor}`,
                    textAlign: `${contestQuestion.button.textStyle[1].textAlign}`,
                    fontWeight: `${contestQuestion.button.textStyle[1].fontWeight}`,
                    fontFamily: `${contestQuestion.button.textStyle[1].fontType}`,
                  }
            }
          >
            {contestQuestion.button.label}
          </Text>
        </Button>
      </View>
    </ScrollView>
  );
}

